
/* forms */

.form{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    
    margin-top: 50px;;
  }
  .form-container{
    display: flex;
    flex-direction: column;
    width:32rem;
    padding: 2rem;
    border: .1rem #c0c0c0 solid;
    border-radius: .5rem;
    list-style-type: none;
  }
  .form-container li{
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    margin-top: 1rem;
  }
  input{
    padding: 1rem;
    border: .1rem #c0c0c0 solid;
    border-radius: .5rem;
  }
  .button.primary{
  background-color: #f0c040;
}
.button.secondary{
  background-color: #f0f0f0;
}
.text-center{
  text-align: center;
}